* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  max-width: 1400px;
  background-color: #fafafa;
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  display: block;
}

.breed-select {
  max-width: 500px;
  letter-spacing: .03em;
  cursor: pointer;
  border: 2px solid #8e8f99;
  border-radius: 5px;
  margin-top: 20px;
  padding: 5px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.1;
}

.loader {
  color: rgba(0, 0, 0, 0);
  text-shadow: 25em 0 #8e8f99;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 800;
  animation: marquee 10s infinite;
  display: inline-block;
  overflow: hidden;
}

.loader:before {
  content: "...";
}

@keyframes marquee {
  to {
    text-shadow: -25em 0 #fff;
  }
}

.error {
  display: none;
}

.breed-card {
  margin-top: 20px;
  display: flex;
}

.cat-img {
  width: 500px;
  margin-right: 50px;
}

.cart-block {
  width: calc(100% - 550px);
  flex-direction: column;
  justify-content: start;
  align-items: start;
  gap: 40px;
  margin: 0;
  display: flex;
}

.cart-title {
  margin: 0;
  font-size: 48px;
}

.breed-description, .breed-temperament {
  margin: 0;
  font-size: 18px;
}

.bold-text {
  font-weight: 700;
}

